body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
  margin: 0;
  padding: 0;
  height: 100%;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

.auth {
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.1)), url("/build/img/fondo.webp");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  min-height: 100vh;
  padding: 5rem 0;
}
@media (max-width: 768px) and (min-height: 700px) {
  .auth {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .auth {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.auth__login {
  background-color: rgba(5, 29, 66, 0.644);
  box-shadow: 0 0 20px 5px rgba(16, 3, 53, 0.61);
  padding: 5rem;
  border-radius: 2rem;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .auth__login {
    width: min(90%, 45rem);
  }
}
.auth__heading {
  margin: 0;
  color: white;
}
.auth__texto {
  text-align: center;
  color: white;
  font-weight: 100;
}

.formulario {
  margin: 5rem 0;
}
.formulario__campo {
  position: relative;
  margin: 3rem 0;
  transition: all 0.3s;
}
.formulario__label {
  position: absolute;
  top: 50%;
  left: 0.5rem;
  transform: translateY(-50%);
  pointer-events: none;
  transition: 0.3s;
  color: rgb(155, 155, 155);
  font-weight: 100;
}
.formulario__label--active {
  top: -0.5rem;
}
.formulario__input {
  border: none;
  padding: 1rem;
  width: 100%;
  height: 5rem;
  background: transparent;
  border: none;
  outline: none;
  padding: 0 3.5rem 0 0.5rem;
  border-bottom: 0.2rem solid rgba(128, 128, 128, 0.445);
  border-radius: 0.5rem;
  transition: all 0.3s;
  color: white;
  font-weight: 100;
}
.formulario__input:-webkit-autofill, .formulario__input:-webkit-autofill:hover, .formulario__input:-webkit-autofill:focus, .formulario__input:-webkit-autofill:active {
  background-color: transparent !important;
  color: white !important;
  -webkit-text-fill-color: white !important;
  transition: background-color 5000s ease-in-out 0s;
}
.formulario__input:focus {
  border-bottom-color: white;
}
.formulario__input:focus ~ .formulario__label, .formulario__input:not(:placeholder-shown) ~ .formulario__label {
  top: -0.5rem;
  color: white;
  font-weight: 900;
}
.formulario__icono {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: rgb(155, 155, 155);
  transition: all 0.3s;
}
.formulario__input:focus ~ .formulario__icono {
  color: white;
}
.formulario__contenedor--boton {
  background-color: #007df4;
  padding: 1rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 2rem;
  margin-top: 1rem;
}
.formulario__contenedor--boton:hover {
  background-color: #0063c1;
  cursor: pointer;
}

.acciones {
  text-align: center;
  color: white;
}
.acciones__enlace {
  color: rgb(145, 145, 145);
  transition: all 0.3s;
}
.acciones__enlace:hover {
  color: #007df4;
}
.acciones__texto {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .acciones__texto {
    display: block;
  }
}

.header {
  min-height: 50rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: 40rem, 40rem, cover, cover;
  background-position: top left, bottom right, center center, center center;
}
.header__contenedor {
  width: min(95%, 120rem);
  margin: 0 auto;
}
.header__navegacion {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  padding: 3rem 0;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    justify-content: flex-end;
  }
}
.header__enlaces {
  font-size: 1.3rem;
  color: #FFFFFF;
}
.header__contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .header__contenido {
    align-items: flex-start;
  }
}
.header__titulo {
  font-size: clamp(2rem, -0.8rem + 10vw, 8rem);
  background: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  color: transparent;
}
.header__texto {
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 2rem;
}
.header__texto--modalidad {
  font-size: 1.5rem;
}
.header__boton {
  background-color: #00c8c2;
  padding: 1rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  text-transform: uppercase;
}
.header__boton:hover {
  background-color: #009591;
  cursor: pointer;
}